.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.App-main {
  margin: 20px;
  padding-bottom: 50px;
}

.App-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #282c34;
  padding: 10px;
  position: fixed;
  width: 100%;
  bottom: 0;
  color: white;
}

.App-nav button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
}

.App-nav button.active {
  font-weight: bold;
  color: #4caf50;
}

.monitor-container {
  margin-top: 50px;
}

.circular-progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px;
  width: 220px;  /* Ajusta el tamaño para que el círculo sea más grande */
  height: 300px;
}

.circular-progress-container h3 {
  margin-top: 15px;  /* Añade espacio entre el círculo y el texto */
  font-size: 18px;
  color: #333;
}

.progress-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.delete-button {
  background-color: #ff0000;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.delete-button:hover {
  background-color: #000000;
}

.history-cards {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}

.history-card {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  padding: 15px 20px;
  text-align: left;
}

.card-header {
  font-weight: bold;
  color: #4caf50;
  margin-bottom: 10px;
  font-size: 16px;
}

.card-body p {
  margin: 4px 0;
  font-size: 14px;
}

.delete-icon-block {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.delete-icon-button {
  background-color: #ff0000;
  color: white;
  border: none;
  padding: 8px 10px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
}

.delete-icon-button:hover {
  background-color: #000000;
}

.voltaje {
  color: red;
}

.corriente {
  color: green;
}

.potencia {
  color: blue;
}
